*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.main_div {
  width: 100%;
  height: 100vh;
  background:#6983aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  
}

.center_div {
  width: 25%;
  height: 85vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 50px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
  margin: auto;
  margin-top: 55px;
  
  
}

h1 {
  color: white;
  background: transparent;
  background-color: #d2302c;
  padding: 6px 0 2px 0;
  text-align: center;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0/3);

}

input{
  text-align: center;
  height: 30px;
  top: 10px;
  margin-left: 45px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  color: #d2302c;
  width: 60%;
  border-bottom: 2px solid #d2302c;
  outline: none;
  
}

button {
  min-height: 35px;
  width: 35px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #d2302c;
  color: #fff;
  font-size: 30px;
  border: none;
  outline: none;
  margin-left: 25px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);

}

button:hover{
  background-color: #20bf6b;
}

ol {
  margin-top: 30px;
}

ol li {
  padding-left: 0px;
  margin-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #d2302c;
  text-transform: capitalize;
  opacity: 0.75;

}

.todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d2302c;
  
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3) ;

}

.fa-times {
opacity: 0.25;
}
.todo_style .fa-times:hover {
  background-color: crimson;
  opacity: 1;

}

.todo_style .fa-times:hover .list_style {
  color: crimson;
}

@media (max-width: 768px){
  .center_div {
    width: 55%;
  }

  .todo_style .fa-timesn{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  button {
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;

  }

}


